/*
*antd from
*getFieldDecorator rules
*app
*
*enum           	                                    string	                                       -
*len	        	                                    number	                                       -
*max	        	                                    number	                                       -
*message        	                                    string	                                       -
*min	        	                                    number	                                       -
*pattern        	                                RegExp	                                       -
*required       	                                    boolean	                                       false
*transform      	                            function(value) => transformedValue:any	       -
*type	        	                        string	                                       'string'
*validator      callback 	        function(rule, value, callback)	               -
*whitespace     	                boolean	                                       false
*author 'lzt'
*/


const user = [
		{min:3,message:'3'},
		{max:9,message:'9'},
		{required:true, message:''}]

const phone = [
		{min:4,message:'11'},
		{max:11,message:'11'},
		{required:true, message:''}]

const strFriendPhone = [
		{min:4,message:'11'},
		{max:11,message:'11'},
		{required:false}]

const password=[
		{max:16,message:'16'},
		{required:true, message:''}]

const passwordAgain=(message,pwd)=>{
	return (rule,value,callback)=>{
	if (value && value !== pwd) {
	      callback(message);
	    } else {
	      callback();
	    }
	}
}

const email =[
		{type: 'email', message: 'The input is not valid E-mail!'},
		{required: false, message: 'Please input your E-mail!'}]

const smsCode = [
		{max:6,message:'6'},
		{required:true,message:''}]

const isDisabled = (getFieldsError)=>{
	// getFieldsError()boolean
	return Object.keys(getFieldsError).some(filed=>getFieldsError[filed])
}

const isChecked =(message)=>{
	return (rule,value,callback)=>{
		if(value){
			callback()
		}else{
			callback(message)
		}
	}
}

export{
	user,
	phone,
	email,
	password,
	isDisabled,
	isChecked,
	strFriendPhone,
	passwordAgain,
	smsCode
} 


